#about {
  padding-top: 80px;
  height: calc(100% - 80px);
  position: relative;
  background-image: url("../../img/background_img_about.jpg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: right;
}
#about .contents-layout .contents-body p {
  color: #333;
  transition: all 1.5s;
}
#about .contents-layout .contents-body p:not(:last-child) {
  margin-bottom: 5rem;
}

#about.active .contents-layout .contents-body p {
  color: #fff;
}

@media (max-width: 1420px) {
  #about .contents-layout {
    height: 100%;
    margin: 0 30px;
  }
  #about .contents-layout .contents-body {
    padding-right: 0px;
  }
}
@media (max-width: 748px) {
  #about {
    background-size: cover;
    background-position: center;
  }
}

